*{
    padding: 0;
    margin: 0%;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
.menubar{
    display: flex;
    justify-content: space-between;
    width: auto; 
    /* border: 2px solid red; */
    align-items: center;
    text-align: center;
   
}

 .menubar ul{
    display: flex;
    /* justify-content: space-between; */
}
.menubar li{
    list-style: none;
    margin-right: 50px;
    font-weight: 600;
    font-size: 1em;
    cursor: pointer;
   

}
.menubar li:hover{
   background-color: crimson;
    padding: 4px 10px;
    border-radius: 10px;
}
.menubar li a{
    text-decoration: none;
    color: #fff;
}

.hamburger{
    position: absolute;
    top: 15px;
    right: 20px;
     width: 25px;
    height: 25px; 
    /* border: 1px solid red; */
    cursor: pointer;
    display: none;
   
}
.hamburger .fa-bars{
    font-size: 1.6em;
    cursor: pointer;
    display: none;
    transition: 0.5s ;
    
}


@media screen and (max-width:991px){
    .navbar{
        padding: 5px 20px;
    }
    .menubar {
        position: absolute;
        display: block;
        height: 100vh;
        width: 100%;
        top: 50px;
        left: -100%;
       
        background-color: palegreen;
       display: flex;
       justify-content: stretch;
       align-items: center;
       flex-direction: column;
      
       
       transition: 0.5s ease-in-out;
       
    }
    .menubar.togg{
        left: 0;
        transition: 0.5s ease-in-out;
        
        
     }
    .menubar li{
        display: block;
        margin-top: 20px;
        /* border-bottom: 1px solid black; */
        margin-right: 0;
        width: 150px;
        
       

    }
    /* .menubar li:last-of-type{
        border-bottom: 0px solid black;

    } */
    .hamburger{
        display: block;
    }
  
    .hamburger .fa-bars{
        display: block;
    }
    .hamburger .fa-times{
        font-size: 1.6em;
        cursor: pointer;
       
        transition: 0.5s ease-in-out;
        
    }
  


}
.banner{
    position: relative;
    width: 100%;
    height: 100vh;
   
    background: url(/reacthero/static/media/myimage3.d21b9838.jpg);
    object-fit: cover;
    background-size: cover;
}

.banner .content{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 10px;
}
.content h1{
    text-transform: uppercase;
    color: #fff;
    font-size: 2.5em;
    letter-spacing: 1px;
}
.content p{
    color: #eee;
}
.content button{
    margin-top: 10px;
    padding: 6px 35px;
    font-size: 1em;
    font-weight: 500;
    background: transparent;
    color: #fff;
    border: 1.5px solid #fff;
    outline: none;
    letter-spacing: 1px;
}.content button:hover{
    background: crimson;
    border: 1.5px solid crimson
}
@media  screen and (max-width:475px){
    .banner .content{
    align-items: flex-start;
    }
    .content h1{
        font-size: 1.5em;
       
    }
    .content p{
        font-size: 0.7em;
      
      
    }
    .content button{
        font-size: 1em;
        padding: 4px 25px;
    
    }
}

    @media screen and (max-width:800px){
        
        .content h1{
            font-size: 1.5em;
           
        }
        .content p{
            font-size: 0.7em;
          
          
        }
        .content button{
            font-size: 1em;
            padding: 4px 25px;
        
        }


}
.card_div{
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;
    padding-bottom: 20px;
}
.card_heading_div{
    display: flex;
    width: 100%;
    justify-content: center;
    padding-top: 20px;
    
}
.card_heading_div h3{
    font-size: 1.7em;
    border-bottom: 2px solid red;
    font-weight: 500;
    letter-spacing: 1px;
}
.card{
    width: 350px;
    height: 300px;
    box-shadow: 0px 2px 2px 2px rgba(0,0,0,0.1);
    margin: 0 30px;
    position: relative;
    border-radius: 10px;
    margin: 20px;
}
.imgBx{
    position: relative;
    width: 100%;
    height: 260px;
    overflow: hidden;
    border-radius: 10px;
   

}
.imgBx img{
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
   /* border-top-left-radius: 10px;
   border-top-right-radius: 10px; */
   transition: 0.5s;
}
.imgBx img:hover{
 transform: scale(1.1);
}
.bxContent{
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 10px;

}
.bxContent button{
    font-size: .8em;
    font-weight: 500;
    letter-spacing: 1px;
    padding: 3px 15px;
    background: transparent;
   
    /* color: #000;
    border: 2px solid #000; */
    box-shadow: 2px 2px 2px 2px rgba(0,0,0.3,0.4);
    border-radius: 5px;
    outline: none;
    border: none;
    

}
.bxContent button:hover{
    background: crimson;
    color: #fff;
    border: none;
    outline: none;
}
.offer{
    position: absolute;
    top: calc(50% - 30px);
    left: 0;
    
    z-index: 2;
    /* display: inline-block; */
    padding: 8px 15px;
    
    background: #000;
    color: #fff;
    transform: rotate(90deg) translateY(50px);
    font-size: 0.8em;

    
}
