.footer_div{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #000;
    padding: auto;
    
   
}
 .footer_content{
     margin: 50px auto;
     width: 200px;
    
    
}
.footer_content h2{
    margin-bottom: 10px;
    color: #fff;
    position: relative;
    font-weight: 300;
    font-size: 1.2em;
   
}
.footer_content h2::before{
    position: absolute;
    content: '';
    left: 0;
    bottom: -5px;
    background-color: #e91e63;
    box-sizing: border-box;
    width: 50px;
    height: 2px;
}
.footer_content ul{
    margin-top: 30px;
}
.footer_content ul li{
    
    list-style: none;
    color: #fff;
    margin-bottom: 5px;
    font-size: .9em;
    color:#BDBDBD;
    cursor: pointer;
    /* transition: 0.5s ease-in-out; */
}
.footer_content ul li:hover{
    transform: translateX(10px);
    color: #FFFF;
    

}
#social{
    display: flex;
    flex-wrap: wrap;
    padding: 0 5px;
   
}
#social li{
    transform: translateX(0);
    display: inline-block;
    width: 30px;
    height: 30px;
    background-color: #455A64;
    border-radius: 50%;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin-left: 5px;
}
#social li i{
     color: #fff;
     margin-top: 8px;
   
}
#social li:hover{
    transform: scale(1.1);
}
#social li i:hover{
    transform: scale(1.1);
    color: #e91e63;
}

